.project {
    margin-bottom: 2.5rem;
}
.project .description{
    margin-left: 1rem;
}

.project img {
    display: block;
    opacity: 1 !important;
    background: transparent;
    filter: none !important;
    -webkit-filter: none !important;
    width: 80%;
    max-width: 50rem;
    max-height: 30rem;
    margin: 1.5rem auto 0 auto;
    padding-right: 1rem;
}

.description > p:first-child {
    margin-top: 0;
}